body{
    .main{
        .row{
            margin-left: 0;
            margin-right: 0;
            justify-content: center;
            padding: 30px 0;
        }
        .main-col{
            .overflow{
                position: relative;
                overflow: hidden;
                width: 100%;
                height: 100%;
                border-radius: 10px;
                -webkit-border-radius: 10px;
                -moz-border-radius: 10px;
                -ms-border-radius: 10px;
                -o-border-radius: 10px;
}
            // position: relative;
            padding: 25px;
            cursor: pointer;
            overflow: hidden;
            height: 350px;
            width: 400px;
            &:hover{
                .img2{
                    opacity: 1;
                }
            }
            .contrast{
                width: 100%;
                height: 100%;
                position: absolute;
                display: flex;
                align-items: center;
                justify-content: center;
                z-index: 3;
                transition: 0.4s;
                -webkit-transition: 0.4s;
                -moz-transition: 0.4s;
                -ms-transition: 0.4s;
                -o-transition: 0.4s;
                &:hover{
                    background-color: rgba(0, 0, 0, 0.596);
                    h4{
                        display: flex;
                        transition: 0.3s;
                        -webkit-transition: 0.3s;
                        -moz-transition: 0.3s;
                        -ms-transition: 0.3s;
                        -o-transition: 0.3s;
                    }
                }
                &:hover + .img1{
                    opacity: 0;
                    transform: scale(1.1);
                    -webkit-transform: scale(1.1);
                    -moz-transform: scale(1.1);
                    -ms-transform: scale(1.1);
                    -o-transform: scale(1.1);
                }
                h4{
                    color: white;
                    font-weight: 400;
                    font-size: 28px;
                    display: none;
                    transition: 0.4s;
                    -webkit-transition: 0.4s;
                    -moz-transition: 0.4s;
                    -ms-transition: 0.4s;
                    -o-transition: 0.4s;
                }
            }
            .img1{
                position: absolute;
                width: 100%;
                height: 100%;
                transition: 0.4s;
                -webkit-transition: 0.4s;
                -moz-transition: 0.4s;
                -ms-transition: 0.4s;
                -o-transition: 0.4s;
            }
            .img2{
                position: absolute;
                opacity: 0;
                width: 100%;
                height: 100%;
                transition: 0.4s;
                -webkit-transition: 0.4s;
                -moz-transition: 0.4s;
                -ms-transition: 0.4s;
                -o-transition: 0.4s;
            }
        }
    }
    .navbar{
        .navbar-nav{
            .nav-item{
                .nav-link{
                    font-weight: 500;
                    color: black;
                    padding-right: 30px;
                    &:hover{
                        color: rgb(248, 54, 119);
                    }
                }
            }
        }
    }
}

::-webkit-scrollbar{
    width: 8px;
}
::-webkit-scrollbar-thumb{
    background-color: rgb(143, 141, 141);
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
}
::-webkit-scrollbar-track{
    background-color: white;
}

.modal-text{
    text-align: justify;
}
.modal-col{
    position: relative;
    padding: 0;
    cursor: pointer;
    overflow: hidden;
    height: 350px;
    .img{
        position: absolute;
        width: 100%;
        height: 100%;
    }
}

.body{
    display: flex;
    flex-wrap: wrap;
}